body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  width: 100%;
  height: 7vh;
  top: 0;
  position: fixed;
  transition: top 0.3s;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-content: center;
          align-content: center;
}

.nav-v1 {
  background-color: none;
  transition: all 1s ease;
}

.nav-v1 span {
  font-size: 1em;
  color: white;
  font-weight: bold;
}

#logo {
  display: inline-block;
  padding: 0.3em;
  margin: 0.4em 0 0.4em 0.2em;
  cursor: pointer;
  cursor: hand;
  color: white;
  font-weight: 300;
  font-size: 1.2em;
}
.nav-v2 {
  background-color: #FAFAFA;
  transition: all 1s ease;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.nav-v2 #logo {
  color: rgb(74, 160, 240);
  font-weight: 300;
  font-size: 1.3em;
  margin: 0.2em 0;
  transition: all 1s ease;
}
.nav-v2 span {
  color: rgb(74, 160, 240);
  font-weight: 300;
  font-size: 1em;
  transition: all 1s ease;
}

.nav-list {
  margin: 1em 0;
}

.nav-list li {
  display: inline-block;
  padding: 0 0.5em;
  color: rgb(74, 160, 240);
  font-weight: 300;
}

.nav-list a {
  position: relative;
  cursor: pointer;
  cursor: hand;
}

.nav-list a::after {
  content: "";
  width: 0%;
  height: 3px;
  background-color: rgb(74, 160, 240);
  position: relative;
  display: block;
  top: 3px;
  transition: all 400ms ease;
}

.nav-list a:hover::after {
  width: 100%;
}

@media (min-width: 550px) {
  .nav-v1 span {
    font-size: 1.3em;
  }
  .nav-v2 span {
    font-size: 1.3em;
  }
}

@media (min-width: 550px) {
  #logo {
    font-size: 1.8em;
  }
  .nav-list li {
    padding: 0 1em;
  }
}

@media (min-width: 650px) {
  .navbar {
    padding: 0.5em 3.5em;
  }
  .nav-list li {
    padding: 0 1.5em;
  }
}

@media (min-width: 950px) {
