.chevron-up {
  cursor: pointer;
  cursor: hand;
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
}

.chevron-up:hover {
  -webkit-animation: pulseHover 1.5s infinite;
          animation: pulseHover 1.5s infinite;
}

@-webkit-keyframes pulse {
  0% {
    color: rgb(85, 84, 84);
  }
  100% {
    color: white;
  }
}

@keyframes pulse {
  0% {
    color: rgb(85, 84, 84);
  }
  100% {
    color: white;
  }
}

@-webkit-keyframes pulseHover {
  0% {
    color: rgb(74, 160, 240);
  }
  100% {
    color: white;
  }
}

@keyframes pulseHover {
  0% {
    color: rgb(74, 160, 240);
  }
  100% {
    color: white;
  }
}

